// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from './components/Layout'
import IndexPage from './pages/Index'
import mock from './mock/mock'
import detail from './pages/Detail'
import Detailforecast from './pages/detail/forecast'
import Detailanalysis from './pages/detail/analysis'
import Detailcount from './pages/detail/count'
import Detailpublish from './pages/detail/publish'
import OrderListPage from './pages/orderList'
import store from './store'
Vue.use(VueRouter)
let router = new VueRouter({
    routes: [{
            path: '/',
            name: 'index',
            component: IndexPage
        },
        {
            path: '/orderList',
            name: 'orderList',
            component: OrderListPage
        },
        {
            path: '/detail',
            component: detail,
            name: 'analysis',
            redirect: '/detail/analysis',
            children: [{
                path: 'forecast',
                name: 'forecast',
                component: Detailforecast
            }, {
                path: 'analysis',
                name: 'analysis',
                component: Detailanalysis
            }, {
                path: 'count',
                name: 'count',
                component: Detailcount
            }, {
                path: 'publish',
                name: 'publish',
                component: Detailpublish
            }]
        }
    ]
})
Vue.config.productionTip = false
new Vue({
    el: "#app",
    router,
    store,
    template: '<Layout/>',
    components: { Layout }
})


