<template>
    <div class="order-wrap">
    <h3></h3>
    <div class="order-list-choose">
      <div class="order-list-option">
        
        <v-selection :two="true" :selectData="products" @onChange="productChange"></v-selection>
      </div>
      <div class="order-list-option">
        
        <v-date-picker @onChange="getStartDate"></v-date-picker>
      </div>

      <div class="order-list-option">
        
        <v-date-picker @onChange="getEndDate"></v-date-picker>
      </div>

      <div class="order-list-option">
        
        <input type="text" v-model.lazy="query" class="order-query">
      </div>
    </div>
    <div class="order-list-table">
      <table>
        <tr>
          <th v-for="head in tableHeads" @click="changeOrderType(head)" :class="{active:head.active}" >{{ head.label }}</th>
        </tr>
        <tr v-for="item in tableData" :key="item.period">
          <td v-for="head in tableHeads">{{ item[head.key] }}</td>
        </tr>
      </table>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import VSelection from "../components/base/selection";
import VDatePicker from "./datePicker";
import _ from 'lodash'
export default {
  data() {
    return {
      productId: 0,
      startDate: "",
      endDate: "",
      currentOrder: "asc",
      products: [
        {
          options: "",
          value: 0
        },
        {
          options: "",
          value: 1
        },
        {
          options: "",
          value: 2
        },
        {
          options: "",
          value: 3
        }
      ],
      tableHeads: [
        {
          label: "",
          key: "orderId",
          active:false
        },
        {
          label: "",
          key: "product",
          active:false
        },
        {
          label: "",
          key: "version",
          active:false
        },
        {
          label: "",
          key: "period",
          active:false
        },
        {
          label: "",
          key: "date",
          active:false
        },
        {
          label: "",
          key: "buyNum",
          active:false
        },
        {
          label: "",
          key: "amount",
          active:false
        }
      ],
      // tableData: [],
      query: ""
    };
  },
  components: {
    VSelection,
    VDatePicker
  },
  watch: {
    query() {
      this.getTableData();
    }
  },
  methods: {
    productChange(obj) {
      this.productId = obj.value;
    },
    getStartDate(date) {
      this.startDate = date;
    },
    getEndDate(date) {
      this.endDate = date;
    },
    getTableData() {
      this.tableHeads.map(item => {
        item.active = false;
      });
      let reqParams = {
        query: this.query,
        productId: this.productId,
        startDate: this.startDate,
        endDate: this.endDate
      };
      axios.post("/api/getOrderList", reqParams).then(
        res => {
          this.tableData = res.data.list;
        },
        err => {
          console.log(err);
        }
      );
    },
    productChange(Obj) {
      this.productId = Obj.value;
      this.getTableData();
    },
    getStartDate() {
      this.getTableData();
    },
    getEndDate() {
      this.getTableData();
    },
    changeOrderType(headItem) {
      this.tableHeads.map(item => {
        item.active = false;
      });
      headItem.active = true;
      if (this.currentOrder === "asc") {
        this.currentOrder = "desc";
      } else if (this.currentOrder === "desc") {
        this.currentOrder = "asc";
      }
      this.tableData.sort(this.compare(headItem.key,this.currentOrder))
    },
    compare(Obj,order){
      return function(a,b){
        console.log(a[Obj])
        console.log(b[Obj])
        var val1 = a[Obj]
        var val2 = b[Obj]
        if(order =='asc'){
          return val1 > val2
        }else{
          return val2 > val1
        }
        
      }
    }
  },
  computed:{
    tableData(){
      return this.$store.getters.getOrderList
    }
  },
  mounted() {
    this.$store.dispatch('fetchOrderList')
    // this.getTableData();
    // console.log(this.$store)
  }
};
</script>
<style scoped>
.order-wrap {
  width: 1200px;
  min-height: 800px;
  margin: 20px auto;
  overflow: hidden;
}
.order-wrap h3 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}
.order-query {
  height: 25px;
  line-height: 25px;
  border: 1px solid #e3e3e3;
  outline: none;
  text-indent: 10px;
}
.order-list-option {
  display: inline-block;
  padding-left: 15px;
}
.order-list-option:first-child {
  padding-left: 0;
}
.order-list-table {
  margin-top: 20px;
}
.order-list-table table {
  width: 100%;
  background: #fff;
}
.order-list-table td,
.order-list-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 10px 0;
}
.order-list-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
  cursor: pointer;
}
.order-list-table th.active {
  background: #35495e;
}
</style>



