<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                <v-counter :counterConfig='counterConfig' @onChange="onParamChange('buyNum',$event)"></v-counter>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-selection :selectData="buyTypes" @onChange="onParamChange('buyType',$event)"></v-selection>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-choose :chooseData="periodList" chooseType="radio" @onChange="onParamChange('period',$event)"></v-choose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-choose :chooseData="versionList" chooseType="check" @onChange="onParamChange('versions',$event)"></v-choose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  {{ price }} 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" @click="ShowPayDialog">
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p></p>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <my-dialog :is-show="isShowPayDialog" @on-close="hidePayDialog">
        <table class="buy-dialog-table">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{ buyNum }}</td>
            <td>{{ buyType.options }}</td>
            <td>{{ period.options }}</td>
            <td>
              <span v-for="item in versions">{{ item.options+'|' }}</span>
            </td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title"></h3>
        <bank-choose @onChange="onChangeBanks"></bank-choose>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          
        </div>
      </my-dialog>
      <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrder"></check-order>
      <my-dialog :is-show="isShowErrDialog" @on-close="hideErrDialog">
        
    </my-dialog>
  </div>
</template>

<script>
import vSelection from "../../components/base/selection";
import vChoose from "../../components/base/choose";
import vCounter from "../../components/base/counter";
import MyDialog from "../../components/base/dialog";
import BankChoose from "../../components/bankChoose";
import CheckOrder from "../../components/checkOrder";
import axios from "axios";
export default {
  data() {
    return {
      buyNum: 0,
      buyType: {},
      versions: {},
      period: {},
      price: 0,
      bankId: 0,
      orderId: "",
      isShowCheckOrder: false,
      isShowErrDialog: false,
      versionList: [
        {
          options: "",
          value: 0
        },
        {
          options: "",
          value: 1
        },
        {
          options: "",
          value: 2
        }
      ],
      periodList: [
        {
          options: "",
          value: 0
        },
        {
          options: "",
          value: 1
        },
        {
          options: "",
          value: 2
        }
      ],
      buyTypes: [
        {
          options: "",
          value: 0
        },
        {
          options: "",
          value: 1
        },
        {
          options: "",
          value: 2
        }
      ],
      counterConfig: {
        max: 1000,
        min: 10
      },
      isShowPayDialog: false
    };
  },
  components: {
    vSelection,
    vChoose,
    vCounter,
    MyDialog,
    BankChoose,
    CheckOrder
  },
  methods: {
    onParamChange(attr, val) {
      this[attr] = val;
      this.getPrice();
    },
    getPrice() {
      let buyVersionsArray = this.versions.map((item, index) => {
        return this.versions[index].value;
      });
      let reqParams = {
        buyNumber: this.buyNum,
        buyType: this.buyType.value,
        period: this.period.value,
        version: buyVersionsArray.join(",")
      };
      axios
        .post("/api/getPrice", reqParams)
        .then(res => {
          this.price = res.data.amount;
        })
        .catch(error => {
          console.log(error);
        });
    },
    ShowPayDialog() {
      this.isShowPayDialog = true;
    },
    hidePayDialog() {
      this.isShowPayDialog = false;
    },
    onChangeBanks(BankObj) {
      this.bankId = BankObj.id;
    },
    confirmBuy() {
      let buyVersionsArray = this.versions.map((item, index) => {
        return this.versions[index].value;
      });
      let reqParams = {
        buyNumber: this.buyNum,
        buyType: this.buyType.value,
        period: this.period.value,
        version: buyVersionsArray.join(","),
        bankId: this.bankId
      };
      axios
        .post("/api/createOrder", reqParams)
        .then(res => {
          this.orderId = res.data.orderId;
          this.isShowPayDialog = false;
          this.isShowCheckOrder = true;
        })
        .catch(error => {
          this.isShowBuyDialog = false;
          this.isShowErrDialog = true;
        });
    },
    hideCheckOrder() {
      this.isShowCheckOrder = false;
    },
    hideErrDialog() {
      this.isShowErrDialog = false;
    }
  },
  mounted() {
    this.buyNum = 10;
    this.buyType = this.buyTypes[0];
    this.versions = [this.versionList[0]];
    this.period = this.periodList[0];
    this.getPrice();
  }
};
</script>

<style>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}
</style>



