<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-counter :counterConfig="counterConfig"></v-counter>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  <v-choose chooseType="check" :chooseData="versionList" @onChange="versionChange"></v-choose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  500 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button">
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p>2020



</p>
      </div>
  </div>
</template>

<script>
// import vSelection from '../../components/selection'
import vChoose from "../../components/base/choose";
import vCounter from "../../components/base/counter";
export default {
  data() {
    return {
      versionList: [
        {
          options: "",
          value: 0
        },
        {
          options: "pdf",
          value: 1
        },
        {
          options: "",
          value: 2
        },
        {
          options: "",
          value: 3
        }
      ],
      counterConfig: {
        max: 100,
        min: 20
      }
    };
  },
  components: {
    vChoose,
    vCounter
  },
  methods:{
    versionChange(obj){
      console.log(obj)
    }
  }
};
</script>

<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}
</style>



